import { Component, OnInit, Injector, HostListener, ViewChild, TemplateRef } from '@angular/core';

import { MatDialog, MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';

import { UserService } from 'src/app/api/controllers/User';
import { LookupService } from 'src/app/api/controllers/Lookup';
import { SharedService } from 'src/app/shared/services/shared.service';
import { ModalService } from 'src/app/shared/services/modal.service';
import { SessionService } from 'src/app/core/session/session.service';
import { jqxGridComponent } from 'src/app/shared/jqwidgets-scripts/jqwidgets-ts/angular_jqxgrid';
import * as _ from 'underscore';
import { Router } from '@angular/router'
import { TranslateService } from '@ngx-translate/core';
@Component({
  selector: 'app-helpdesk-setup',
  templateUrl: './helpdesk-setup.component.html',
  styleUrls: ['./helpdesk-setup.component.scss']
})
export class HelpdeskSetupComponent implements OnInit {
  navArray:any;

  constructor(private translateService: TranslateService) {}
 
  ngOnInit() {
    
    this.navArray = [
      {link:'private-category', name: 'Private Category'},
      {link:'common-category', name: 'Common Category'} 
    ]
  
  }

  tab(tabTitle){
    let retTitle = "";
    this.translateService.get('SERVICE.HELPDESK.SETTINGS').subscribe((data:any)=> {
      if(tabTitle === "Private Category") {
        retTitle = `${data.PRIVATECATEGORY}`
      } else {
        retTitle = `${data.COMMONCATEGORY}`
      }
      
    });
    return retTitle;
  }
}

